/* Global Styles */
:root {
  --primary-color: #17a2b8;
  --dark-color: #343a40;
  --light-color: #f4f4f4;
  --danger-color: #dc3545;
  --success-color: #28a745;
}
/* test */
.alert {
  position: absolute;
  top: 120px;
  width: calc(100% - 800px);
  margin-left: 400px;
  letter-spacing: 1px;
  text-align: center;
  border-radius: 5px;
  visibility: 1;
  height: 70px;
  padding-top: 25px;
  opacity: 0.6;
  z-index: 2299;
  background: #f4f4f4;
  background: var(--light-color);
  color: #333;
  transition: 0.5s ease;
  margin-bottom: 5px;
}

.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
  background: #17a2b8;
  background: var(--primary-color);
  color: #fff;
}

.btn-light,
.bg-light,
.badge-light,
.alert-light {
  background: #f4f4f4;
  background: var(--light-color);
  color: #333;
}

.btn-dark,
.bg-dark,
.badge-dark,
.alert-dark {
  background: #343a40;
  background: var(--dark-color);
  color: #fff;
}

.btn-danger,
.bg-danger,
.badge-danger,
.alert-danger {
  background: #dc3545;
  background: var(--danger-color);
  color: #fff;
  transition: 5s ease;
}

.btn-success,
.bg-success,
.badge-success,
.alert-success {
  background: #28a745;
  background: var(--success-color);
  color: #fff;
  transition: 5s ease;
}

.btn-white,
.bg-white,
.badge-white,
.alert-white {
  background: #fff;
  color: #333;
  border: #ccc solid 1px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

body {
  font-family: 'Roboto', sans-serif;
}

nav {
  height: 80px;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 100;
  background-color: #7ba5cf;
  margin-bottom: 100px;
}

.logo {
  color: #fff;
  font-size: 35px;
  line-height: 80px;
  padding: 0 100px;
  font-weight: bold;
}
.logo a {
  padding: 5px;
  border-radius: 5px;
}
nav > img {
  height: 30px;
  position: absolute;
  left: 55px;
  top: 25px;
  object-fit: contain;
  margin-bottom: 25px;
}
nav .elementler {
  float: right;
  margin-right: 70px;
}

nav ul li {
  display: inline-block;
  line-height: 80px;
  margin: 0 5px;
  margin-right: 50px;
}

nav ul li a {
  color: #fff;
  padding: 7px 13px;
  border-radius: 3px;
  font-size: 20px;
}
a {
  color: #fff;
}
a:hover {
  background-color: #3872ac;
  transition: 0.5s;
}

.checkbtn {
  font-size: 30px;
  color: white;
  float: right;
  line-height: 80px;
  margin-right: 40px;
  cursor: pointer;
  display: none;
}

#check {
  display: none;
}

@media (max-width: 952px) {
  label.logo {
    font-size: 30px;
    padding-left: 50px;
  }
  nav ul li a {
    font-size: 16px;
  }
  nav > img {
    left: 7px;
  }
}

@media (max-width: 858px) {
  .checkbtn {
    display: block;
    margin-top: 20px;
  }
  ul {
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: #2c3e50;
    top: 80px;
    left: -100%;
    text-align: center;
    transition: all 0.5s;
  }
  nav ul li {
    display: block;
    margin: 50px 0;
    line-height: 30px;
  }
  nav ul li a {
    font-size: 20px;
  }
  a:hover {
    background: none;
    color: #0082e6;
  }
  #check:checked ~ ul {
    left: 0;
  }
}

